//远程处理跟用户相关的数据 比如登录，注册
import axios from "axios";
//使用账号密码登录
export async function login(loginInfo){
    //post http://study.yuanjin.tech/api/user/login
    var resp = await axios.post(
        "/api/user/login",
        loginInfo
    );

    var token = resp.headers.authorization; //登录成功以后拿到服务器令牌
    if(token){
        //把令牌保存下来
        localStorage.setItem("token",token);
    }
    console.log(token);
    return resp.data;
}

//使用保存的令牌从服务器换取登录信息
export async function whoAmI(){
    //gei http://study.yuanjin.tech/api/user/whoami
    //authorization:bearer tokenxxxx
    //首先拿到服务器令牌
    var token = localStorage.getItem("token");
    //判断令牌是否存在
    if(!token){
        return null;
    }
    //如果有服务器令牌，向服务器请求
    var resp = await axios.get("/api/user/whoami",{
        headers:{
            authorization:`bearer ${token}`,
        },
    });
    //将获取的用户信息返回出去
    return resp.data.data;
}

//注销令牌
export  function loginOut(){
    //将保存的令牌注销
    localStorage.removeItem("token");
}

//注册
export async function reg(userInfo){
    //post http://study.yuanjin.tech/api/user/reg
    var resp = await axios.post("/api/user/reg",userInfo);
    return resp.data;
}
