<template>
<div class="header">
    <div class="header-container">
        <div class="container">
            <div class="logo">
                <!-- 声明式导航 -->
                <router-link :to="{name:'Home'}">
                    <img src="../assets/img/ceshi.jpg" alt="">
                </router-link>
            </div>
            <ul class="nav">
                <li><router-link :to="{name:'Home'}">首页</router-link></li>
                <!-- <li>
                    {{isLoading}}
                </li> -->
                <li v-if="isLoading">Loading...</li>
                <li v-for="item in data.slice(0,5)" :key="item.channelId">
                    <router-link 
                    :to="{
                        name:'ChannelNews',
                        params:{
                        id:item.channelId,
                        },
                        }">{{item.name}}</router-link> 
                </li>
            </ul>
            <div class="user">
                <!-- 情况1：正在远程加载中 -->
                <span v-if="isLogining">Loading...</span>
                <!-- 情况2：当前有登录用户 -->
                <!--当多个元素同组数据 例如v-if v-else v-for 你又不想改变元素结构 可以使用template元素，template元素是一个没有任何意义的元素-->
                <template v-else-if="loginUser">
                    <router-link :to="{name:'Personal'}" href="">{{loginUser.nickname}}</router-link>
                    <a href="" @click.prevent="handleLoginOut">退出登录</a>
                </template>
                <!-- 情况3：没有登录用户 -->
                <template v-else>
                    <router-link :to="{name:'Login'}">登录</router-link>
                    <router-link :to="{name:'Reg'}">注册</router-link>
                </template> 
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { mapState }  from 'vuex';

export default {
    computed:{
        ...mapState("channels", ["data","isLoading"]),
        ...mapState("loginUser",{
            //这样写可以避免计算属性冲突
            loginUser:"data",
            isLogining:"isLoading",
        }),
    },
    
    methods:{
        //退出登录
        handleLoginOut(){
            this.$store.dispatch("loginUser/loginOut");
            //退出登录后，跳转登录界面
            this.$router.push({name:"Login"});
        },
    }
};
</script>

<style scoped>
.header {
    height: 60px;
}
.header-container {
    height: 60px;
    background: #000;
    color: #fff;
    line-height: 60px;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    display: flex;
}
.container {
    display: flex;
}
.logo a{
    display: flex;
    align-items: center;
    height: 100%;
}
.logo img {
    width: 42px;
    height: 42px;
}
.nav{
    margin: 0 60px;
    display: flex;
    flex-grow: 1;
    justify-content: space-between;
}
.nav a{
    display: block;
    padding: 0 30pxs;
}
.nav .router-link-exact-active{
    color: #fcb85f;
}
.user{
    font-size: 14px;
}
.user *{
    margin-left: 10px;
}
.header a{
    color: #fff;
}
</style>