//页面跳转
export default {
    routes:[
        {
            path: "/",
            name:"Home",
            component: ()=>import("@/views/Home"),
        },
        {
            path: "/login",
            name:"Login",
            component: ()=>import("@/views/Login"),
        },
        {
            path: "/reg",
            name:"Reg",
            component: ()=>import("@/views/Reg"),
        },
        {
            path: "/persona",
            name:"Personal",
            component: ()=>import("@/views/Personal"),
            //配置的目的是可以在导航守卫里获取到
            meta:{
                auth:true,
            }
        },
        {
            path: "/auth",
            name:"Auth",
            component: ()=>import("@/views/Auth"),
            //配置的目的是可以在导航守卫里获取到
        },
        //动态路径
        {
            path:"/channel/:id",
            name:"ChannelNews",
            component: ()=>import("@/views/ChannelNews"),
        },
        {
            path:"*",//表示匹配所有路径
            name:"404",
            component: ()=>import("@/views/NotFound"),
        },
    ],
    mode:"history",
}