//channels的仓库数据
import{getNewsChannels} from '../services/newService'
export default {
    namespaced: true,//开启命名空间
    state:{
        data: [],
        isLoading: false,
    },

    mutations:{
        //这里配置多种变异方式
        //state: 原来的状态
        //payload 负荷 true或false   //不一定取payload名字
        setIsLoading(state,payload) {
            state.isLoading = payload;
        },
        //payload 负荷，约定是一个数组
        setData(state,payload){
            state.data = payload;
        },
    },
    //如果要使用副作用操作就要用actions
    actions:{
        async fetchDatas(context){
            var channels = await getNewsChannels();  
            //设置isLoading为true
            context.commit('setIsLoading',true);     
            //设置data为channels
            context.commit("setData",channels);
            //设置isLoading为flase
            context.commit('setIsLoading',false);    
        }
    },
    // actions:{
    //     async fetchDatas(context){
    //         //设置isLoading为true
    //         context.commit("setIsLoading",ture);
    //         var channels = await getNewsChannels();
    //         //设置data为channels
    //         context.commit("setData",channels);
    //         //设置isLoading为flase
    //         context.commit("setIsLoading",false);
    //     }
    // },
};





/* 1.数据的改动
    必须要提交commit一个mutation 
    在vue中，提交mutations是数据变化的唯一原因
    在mutations中不能出现副作用操作
    什么是副作用操作：
        1.改动或使用外部的数据
        2.ajax
        3.localstorage
        4.其他异步行为
   2.处理副作用      
    触发'dispatch'一个action
*/ 