//登录用户的仓库数据
import { login,loginOut,whoAmI } from "../services/userService";
export default{
    namespaced: true,//开启命名空间
    state:{
        data:null,//当前登录的用户
        isLoading:false,//是否正在远程加载中
    },
    mutations:{
        setIsLoading(state,payload){
            state.isLoading = payload;
        },
        setData(state,payload){
            state.data = payload;
        },
    },
    actions:{
        //使用账号密码登录
        async login(context,payload){
            context.commit("setIsLoading",true);//登录的时候设置正在加载中设为true
            var resp = await login(payload);//进行登录
            var result = false; //判断是否成功返回的值
            //判断是否登录成功
            if(resp.code === 0){
                //登录成功
                context.commit("setData",resp.data);
                result = true;
            }
                //登录失败
            context.commit("setIsLoading",false);//登录完成后，不管成功还是失败设为false
            return result;
        },
        //使用服务器令牌登录
        async whoAmI(context){
            context.commit("setIsLoading",true);
            var resp = await whoAmI();//通过whoAmI得到我到底是谁
            context.commit("setData",resp);
            context.commit("setIsLoading",false);
        },
        //注销登录
        loginOut(context){
            loginOut();
            context.commit("setData",null);
        },
    },
};